// NoticeNotification
import React from 'react'
import classNames from 'classnames'
import '../style/notice.scss'

function empty() {}

class Notice extends React.Component {
    constructor (props) {
        super(props);
        this.state = {
            shouldClose: false, // 
        }
    }
    componentDidMount () {
        if(this.props.duration > 0){
            this.closeTimer = setTimeout(() => {
                this.close();
            }, this.props.duration - 300); // 300
        }
    }
    componentWillUnmount () {
        //  
        this.clearCloseTimer();
    }
    clearCloseTimer () {
        if (this.closeTimer) {
            clearTimeout(this.closeTimer);
            this.closeTimer = null;
        }
    }
    close () {
        //  
        // 
        //  
        this.clearCloseTimer();
        const _this = this;
        _this.setState({shouldClose: true});
        this.timer = setTimeout(()=>{
            if(this.props.onClose){
                this.props.onClose();
            }
            clearTimeout(_this.timer);
        }, 300);
    }
    render () {
        const {shouldClose} = this.state;
        const {prefixCls, type, iconClass, content} = this.props;

        return (
            <div
                className={classNames([prefixCls,
                    {'info': type === 'info'},
                    {'success': type === 'success'},
                    {'warning': type === 'warning'},
                    {'error': type === 'error'},
                    {'leave': shouldClose}
                ])}
            >
                {iconClass ? <div className={`${prefixCls}-icon`}><span className={classNames(['fa', iconClass])} /></div> : null}
                <div className={`${prefixCls}-content`}>{content}</div>
            </div>
        )
    }
}

Notice.propTypes = {
    duration: React.PropTypes.number.isRequired, // Notice
    prefixCls: React.PropTypes.string, // class
    type: React.PropTypes.oneOf(['info', 'success', 'warning', 'error']), // notice
    iconClass: React.PropTypes.string, // iconclass
    content: React.PropTypes.any, // Notice
    onClose: React.PropTypes.func // 
};

Notice.defaultProps = {
    prefixCls: 'zby-notice',
    duration: 3000,
    onClose: empty
};

export default Notice


